html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Roboto", sans-serif;
  color: #6e6e6e; }

.nav {
  position: fixed;
  display: flex;
  width: 100%;
  background-color: #fff;
  height: 100px; }
  .nav__container {
    display: flex;
    width: 100%; }
  .nav__logo {
    position: fixed;
    display: flex;
    align-self: center;
    width: 200px;
    margin-left: 2%; }
  .nav__list {
    display: flex;
    margin-right: 15vh;
    list-style-type: none; }
  .nav__button {
    padding: 15px 25px;
    border-radius: 4px;
    font-size: 1.125em;
    display: inline-block; }
  .nav__link {
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
    height: 40px;
    width: 70px;
    text-decoration: none; }
    .nav__link:hover {
      transition: all 0.1s;
      color: #DF4D54; }

.header__container {
  display: flex;
  flex-direction: row;
  height: 100px;
  justify-content: center;
  align-items: center;
  margin-top: 0; }
  @media (max-width: 950px) {
    .header__container {
      display: flex;
      flex-direction: column; } }

.header__title {
  display: flex;
  height: 100px;
  font-size: 1.5rem;
  align-items: center;
  justify-content: center;
  margin-top: 0;
  color: white;
  font-weight: 300; }
  .header__title--first {
    width: 50%;
    height: 100px;
    background-color: #DF4D54; }
    @media (max-width: 950px) {
      .header__title--first {
        width: 100%;
        margin: 0; } }
  .header__title--second {
    width: 50%;
    height: 100px;
    background-color: #0076FF; }
    @media (max-width: 950px) {
      .header__title--second {
        width: 100%;
        margin: 0; } }

.header__image {
  margin-top: 100px;
  width: 100%; }

.header__slant {
  height: 100px;
  margin-top: -16px; }
  @media (max-width: 950px) {
    .header__slant {
      display: none; } }

.offer__types {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 5vw 4vw 0 4vw; }
  @media (max-width: 750px) {
    .offer__types {
      display: flex;
      flex-direction: column;
      margin: 5vw 8vw 0 8vw;
      align-items: center; } }

.offer__list {
  list-style: url("../assets/img/list-check.png");
  margin: 0;
  padding: 1.4vw; }

.offer__item {
  display: list-item;
  font-size: 1.2rem;
  line-height: 3rem; }

.offer__type {
  display: flex;
  flex-direction: column;
  width: 30%; }
  @media (max-width: 750px) {
    .offer__type {
      width: 100%; } }

.offer__image {
  width: 120px;
  height: 120px;
  align-self: center; }
  .offer__image:hover {
    transform: scale(1.15);
    transition: 0.2s; }

.features {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-align: center;
  margin: 0 5vw 0 5vw; }
  @media (max-width: 1100px) {
    .features {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      column-gap: 2vw;
      row-gap: 2vw; } }
  @media (max-width: 600px) {
    .features {
      grid-template-columns: 1fr; } }

.feature {
  margin: 3vw 0 3vw 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 20vw;
  height: 12vw;
  background-color: #e4e9ec; }
  .feature:hover {
    transform: scale(1.15);
    transition: 0.2s; }
  @media (max-width: 1100px) {
    .feature {
      display: flex;
      align-self: center;
      justify-self: center;
      height: 80%;
      width: 80%; } }

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 5vw 0 5vw 0;
  width: 100%; }
  .about__background {
    background-image: url("../assets/img/foto-onas-xl.jpg");
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 300px;
    width: 100%; }
    .about__background--second {
      background-image: url("../assets/img/foto-flota-xl.jpg"); }
  .about__motto {
    color: white;
    font-size: 1.8rem;
    line-height: 5rem;
    padding: 0 15vw 0 15vw; }
    @media (max-width: 1100px) {
      .about__motto {
        font-size: 1.5rem;
        line-height: 4rem; } }
    @media (max-width: 750px) {
      .about__motto {
        font-size: 1.3rem;
        padding: 0 8vw 0 8vw; } }
    @media (max-width: 550px) {
      .about__motto {
        line-height: 3rem; } }
  .about__context {
    width: 70%;
    font-size: 1.1rem;
    line-height: 3rem;
    margin: 2vw 0 3vw 0; }

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 40vw;
  margin-bottom: 1vw;
  color: white;
  font-size: 1.5rem;
  background: #0076ff;
  background: linear-gradient(34deg, #0076ff 11%, #df4d54 77%); }
  .footer__title {
    color: black; }
  @media (max-width: 1100px) {
    .footer {
      font-size: 1.3rem; } }
  @media (max-width: 650px) {
    .footer {
      height: 60vw; } }
  @media (max-width: 425px) {
    .footer {
      height: 80vw; } }
